















import { Component, Prop, Vue } from 'vue-property-decorator';

@Component
export default class PlacesFilter extends Vue {
  get search() {
    return this.$store.getters.filter.search;
  }

  set search(term) {
    this.$store.commit('updateFilterSearch', term);
  }

  get tags() {
    return this.$store.getters.tags;
  }

  isTagActive(tag:string):boolean {
    return this.$store.getters.filter.tags.indexOf(tag) >= 0;
  }

  toggle(tag:string):void {
    this.$store.commit('toggleTag', tag);
  }
}
