import { Util } from '@/Util';
import { EmptyFilter } from './Dto';
import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import { Place } from '@/Dto';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    places: new Array<Place>(),
    tags: new Array<string>(),
    filter: new EmptyFilter(),
  },
  mutations: {
    updatePlaces(state, payload) {
      state.places = payload || new Array<Place>();
      let tags = new Array<string>();

      for (const place of state.places) {
        if (place.tags) {
          tags = tags.concat(place.tags);
        }
      }

      state.tags = tags.filter((tag, index, self) => self.indexOf(tag) === index);
    },
    updateFilterSearch(state, payload) {
      state.filter.search = payload || '';
    },
    toggleTag(state, payload) {
      if (payload) {
        if (state.filter.tags.indexOf(payload) < 0) {
          state.filter.tags.push(payload);
        } else {
          state.filter.tags = state.filter.tags.filter(tag => tag !== payload);
        }
      }
    },
  },
  getters: {
    filteredPlaces: (state, getters) => state.places.filter((place) => {
      // filter by 'search term'
      if (!Util.contains(place.name, state.filter.search)) {
        return false;
      }
      // filter by 'tags'
      for (let tag of state.filter.tags) {
        if (place.tags && place.tags.indexOf(tag) < 0) {
          return false;
        }
      }
      return true;
    }),
    tags: (state, getters) => state.tags,
    filter: (state, getters) => state.filter,
  },
  actions: {
    fetchPlaces({ commit }) {
      axios.get('http://api.eater.nz/places.json').then(
        response => commit('updatePlaces', response.data),
        (error) => {
          console.error(error);
        },
      );
    },
  },
});
