



















import { Component, Prop, Vue } from 'vue-property-decorator';
import { Place } from '@/Dto';

@Component
export default class PlacesList extends Vue {
  columns = 4;

  address(place: Place) {
    let address = place.address.number + ' ' + place.address.street;
    address += ', ' + place.address.suburb;
    address += place.address.district ? ', ' + place.address.district : '';
    return address;
  }

  get rows() {
    const rows = new Array();
    let i = 0;
    for (let place of this.$store.getters.filteredPlaces) {
      let index = Math.floor(i++ / this.columns);
      if (!rows[index]) {
        rows[index] = new Array();
      }
      rows[index].push(place);
    }
    return rows;
  }
}
